import React from 'react'
import styled from 'styled-components'
import { Container, Row, Col } from 'react-awesome-styled-grid'
import siteConfig from '../../data/siteConfig'

import Layout from '../components/layout'
import SEO from '../components/SEO'
import Wrapper from '../components/wrapper'
import About from '../components/about'
import Skills from '../components/skills'

class DevPath extends React.Component {
  render () {
    const title = 'Dev Diary'
    return (
      <Layout location={this.props.location}>
        <SEO
          title={title}
          keywords={['gatsbyjs', 'react', 'developer', 'kubernetes', 'vue', 'javascript']} 
        />

        <Wrapper className={this.props.className} >
          <Container className="page-content" fluid>
            <Row>
              <Col xs={6} sm={6} lg={5}>
                <About title='Learning Path' text={siteConfig.diaryDescription}/>
              </Col>
              <Col xs={6} sm={6} lg={5}>
                <Skills title='Building Skills' skills={siteConfig.diarySkills} />
              </Col>
            </Row>
          </Container>
        </Wrapper>
      </Layout>
    )
  }
}

export default styled(DevPath)`
  .page-content {
    max-width: 100%;
    margin-bottom: 40px;
    margin-top: 135px;
  }
`
